h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-size: 1em;
}

p {
    margin: 0;
}

b {
    font-weight: 500;
}

.title {
    margin-bottom: 80px;
    margin-right: -4px;
    font-size: 40px;
    font-weight: 500;
    letter-spacing: 4px;
    text-align: center;

    &::after {
        content: '';
        display: block;
        width: 145px;
        height: 3px;
        margin: 17px auto 0;
        background-image: linear-gradient(to right, #F8D074, #56B770, #1A8FAD);
    }

    @include breakpoint-down('desktop') {
        margin-bottom: 56px;
        font-size: 32px;

        &::after {
            width: 120px;
            margin: 13px auto 0;
        }
    }

    @include breakpoint-down('tablet') {
        margin-bottom: 40px;
        font-size: 26px;

        &::after {
            width: 105px;
            margin: 10px auto 0;
        }
    }
}