.block {
    padding: 100px 0 120px;

    &-wrap {
        box-sizing: border-box;
        max-width: 1080px;
        margin: 0 auto;
        padding: 0 20px;

        &.-lg {
            max-width: 1240px;
        }

        &.-full {
            max-width: none;
            padding: 0;
        }
    }

    &-text {
        margin-top: -30px;
        margin-bottom: 100px;
        font-size: 22px;
        color: #8b8b8b;

        &.-bu {
            font-size: 24px;
            text-align: center;
            color: #4f969d;
        }
    }

    &-action {
        margin-top: 80px;
        text-align: center;
    }

    &.-contact {
        padding-bottom: 0;
    }

    @include breakpoint-down('desktop') {
        padding: 80px 0 90px;

        &-wrap {
            &.-wording {
                max-width: 880px;
            }
        }

        &-text {
            margin-top: -20px;
            margin-bottom: 70px;
            font-size: 19px;

            &.-bu {
                font-size: 21px;
            }
        }

        &-action {
            margin-top: 60px;
        }
    }

    @include breakpoint-down('tablet') {
        padding: 60px 0 80px;

        &-wrap {
            &.-wording {
                max-width: 740px;
            }
        }

        &-text {
            margin-top: -10px;
            margin-bottom: 50px;
            font-size: 16px;

            &.-bu {
                font-size: 18px;
            }
        }

        &-action {
            margin-top: 50px;
        }
    }

    @include breakpoint-down('mobile') {
        &-action {
            margin-top: 40px;
        }
    }
}

.articleContent {
    &-head {
        margin-bottom: 70px;
    }

    &-date {
        font-size: 19px;
        font-weight: 500;
        letter-spacing: 2px;
        color: #8b8b8b;
    }

    &-title {
        margin-top: 4px;
        font-size: 40px;
        font-weight: 500;
        letter-spacing: 4px;

        &::after {
            content: '';
            display: block;
            width: 145px;
            height: 3px;
            margin-top: 17px;
            background-image: linear-gradient(to right, #F8D074, #56B770, #1A8FAD);
        }
    }

    &-content {
        img {
            max-width: 100%;
            border-radius: 0 0 0 50px;
        }

        p {
            font-size: 21px;
            line-height: 1.8;
        }

        *+* {
            margin-top: 40px;
        }
    }

    @include breakpoint-down('desktop') {
        &-head {
            margin-bottom: 50px;
        }

        &-date {
            font-size: 16px;
        }

        &-title {
            margin-top: 2px;
            font-size: 36px;

            &::after {
                margin-top: 15px;
            }
        }

        &-content {
            p {
                font-size: 18px;
            }

            *+* {
                margin-top: 35px;
            }
        }
    }

    @include breakpoint-down('tablet') {
        &-head {
            margin-bottom: 40px;
        }

        &-date {
            font-size: 14px;
        }

        &-title {
            font-size: 30px;

            &::after {
                width: 120px;
                margin-top: 10px;
            }
        }

        &-content {
            p {
                font-size: 16px;
            }

            *+* {
                margin-top: 30px;
            }
        }
    }

    @include breakpoint-down('mobile') {
        &-head {
            margin-bottom: 35px;
        }

        &-date {
            font-size: 13px;
        }

        &-title {
            font-size: 26px;
            letter-spacing: 2px;

            &::after {
                width: 110px;
            }
        }

        &-content {
            img {
                border-radius: 0 0 0 30px;
            }

            p {
                font-size: 16px;
            }

            *+* {
                margin-top: 20px;
            }
        }
    }
}

.contact {
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: -20px;
    padding-bottom: 165px;
    text-align: center;

    &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 340px;
        background-image: linear-gradient(to left, #fdce63, #3abd67, #0094ac);
        opacity: 0.5;
    }

    &-item {
        position: relative;
        box-sizing: border-box;
        flex: 0 1 250px;
        margin: 0 13px;
        padding: 30px 10px 30px;
        text-decoration: none;
        box-shadow: 5.7px 5.7px 28.5px 1.5px rgba(0, 0, 0, 0.28);
        background-color: #fff;
        z-index: 1;
    }

    &-pic {
        box-sizing: border-box;
        width: 115px;
        height: 115px;
        margin: auto;
        padding: 14px;

        img {
            width: 100%;
        }
    }

    &-text {
        display: block;
        margin-top: 10px;
        font-size: 20px;
        text-decoration: none;
        color: #4f969d;
    }

    @include breakpoint-down('desktop') {
        padding-bottom: 145px;

        &::before {
            height: 300px;
        }

        &-item {
            flex: 0 1 220px;
        }

        &-pic {
            width: 100px;
            height: 100px;
        }

        &-text {
            margin-top: 5px;
            font-size: 18px;
        }
    }

    @include breakpoint-down('tablet') {
        margin-top: 0;
        padding-bottom: 115px;

        &::before {
            height: 240px;
        }

        &-item {
            flex: 0 1 170px;
            margin: 0 8px;
            padding: 20px 10px 20px;
        }

        &-pic {
            width: 80px;
            height: 80px;
            padding: 8px;
        }

        &-text {
            font-size: 16px;
        }
    }

    @include breakpoint-down('mobile') {
        flex-wrap: wrap;
        margin: -8px -8px 0;
        padding: 0 20px;
        padding-bottom: 80px;

        &::before {
            height: 360px;
        }


        &-item {
            flex: 0 0 auto;
            width: calc((100% - 32px)/2);
            margin: 8px;
            padding: 20px 5px 20px;
            box-shadow: 3px 3px 15px 0 rgba(0, 0, 0, 0.28);
        }

        &-pic {
            width: 70px;
            height: 70px;
        }
    }
}