.insideBanner {
    position: relative;
    height: 410px;
    overflow: hidden;

    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 10px;
        background-image: linear-gradient(to right, #1A8FAD, #56B770, #F8D074, #56B770, #1A8FAD);
        background-size: 200% 100%;
        // background-image: linear-gradient(to right, #F8D074, #56B770, #1A8FAD);
        animation: gradient 5s linear infinite reverse;
    }

    &-pic {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        min-width: 1920px;
        text-align: center;

        img {
            width: 100%;

            &.-mb {
                display: none;
            }
        }
    }

    @include breakpoint-down('desktop') {
        height: 310px;

        &-pic {
            min-width: 1440px;
        }
    }

    @include breakpoint-down('tablet') {
        height: 250px;

        &::after {
            height: 7px;
        }

        &-pic {
            min-width: 1200px;
        }
    }

    @include breakpoint-down('mobile') {
        height: auto;

        &-pic {
            position: static;
            min-width: auto;
            transform: none;

            img {
                &.-pc {
                    display: none;
                }

                &.-mb {
                    display: inline-block;
                }
            }
        }
    }
}