.btn {
    box-sizing: border-box;
    display: inline-block;
    height: 36px;
    padding: 4px 28px;
    font-size: 17px;
    font-weight: 500;
    color: #0091b1;
    text-decoration: none;
    border-radius: 18px;
    border: solid 1px #149cb3;
    background-image: linear-gradient(to right, #fdce63, #3abd67, #0094ac 50%, #fff 55%, #fff 100%);
    background-size: 220%;
    background-position: 99% 0;
    transition: .3s;

    &:hover {
        color: #fff;
        border-color: #fff;
        background-position: 0 0;
    }

    &.-solid {
        color: #fff;
        border-color: #fff;
        background-position: 0 0;

        &:hover {
            color: #0091b1;
            border-color: #0091b1;
            background-position: 100% 0;
        }
    }

    &.-lg {
        height: 50px;
        padding: 1px 28px 2px;
        font-size: 30px;
        border-radius: 25px;
    }

    &.-arrowL {
        padding-left: 20px;
    }

    &.-arrowL & {
        &-icon {
            display: flex;
            justify-content: center;
            align-items: center;

            svg {
                flex: 0 0 auto;
                width: 24px;
                margin-right: 15px;

                path {
                    transition: .3s;
                }
            }
        }
    }

    &.-arrowL:hover & {
        &-icon {
            svg path {
                fill: #0091b1;
            }
        }
    }

    &.-wt {
        min-width: 150px;
        background: #fff;
        border-color: #fff;

        &:hover {
            color: #fff;
            background: #0091b1;
            border-color: #0091b1;
        }
    }

    @include breakpoint-down('tablet') {
        height: 29px;
        padding: 2px 20px;
        font-size: 15px;

        &.-lg {
            height: 37px;
            padding: 2px 20px 3px;
            font-size: 20px;
        }

        &.-arrowL {
            padding-left: 15px;
        }

        &.-arrowL & {
            &-icon {
                svg {
                    width: 18px;
                    margin-right: 10px;
                }
            }
        }

        &.-wt {
            min-width: 110px;
        }
    }
}