.cardGroup {
    display: flex;
    flex-wrap: wrap;
    margin: -35px -30px;

    &-item {
        flex: 0 0 auto;
        width: calc((100% - 180px)/3);
        margin: 35px 30px;
    }

    @include breakpoint-down('laptop') {
        margin: -20px;

        &-item {
            width: calc((100% - 120px)/3);
            margin: 20px;
        }
    }

    @include breakpoint-down('tablet') {
        margin: -10px;

        &-item {
            width: calc((100% - 60px)/3);
            margin: 10px;
        }
    }

    @include breakpoint-down('mobile') {
        display: block;
        margin: 0;
        padding: 0 30px;

        &-item {
            width: 100%;
            margin: 0;

            &+& {
                margin-top: 30px;
            }
        }
    }
}

.card {
    display: flex;
    flex-direction: column;
    padding: 26px 26px 18px;
    box-shadow: 0 0 46px 0 rgba(0, 0, 0, 0.28);
    background-color: #fff;

    &-pic {
        border-radius: 0 0 0 50px;
        overflow: hidden;

        img {
            width: 100%;
        }
    }

    &-title {
        flex: 1 0 auto;
        min-height: 86px;
        margin: 10px 0;
        font-size: 20px;
        font-weight: 500;
    }

    &-tool {
        justify-self: flex-end;
        display: flex;
        align-items: flex-end;
    }

    &-date {
        flex: 1;
        font-size: 20px;
        color: #8b8b8b;
    }

    @include breakpoint-down('laptop') {
        padding: 20px 20px 15px;

        &-title {
            min-height: 76px;
            font-size: 18px;
        }

        &-date {
            font-size: 18px;
        }
    }

    @include breakpoint-down('tablet') {
        padding: 15px 15px 10px;

        &-title {
            min-height: 60px;
            font-size: 16px;
        }

        &-date {
            font-size: 16px;
        }
    }
}